/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { createApp } from 'vue'
import {
  ElButton,
  ElCol,
  ElCarousel,
  ElCarouselItem,
  ElDivider,
  ElForm,
  ElFormItem,
  ElIcon,
  ElImage,
  ElInput,
  ElLoading,
  ElRow,
  ElTable,
  ElTableColumn,
  ElUpload,
} from 'element-plus'
import './element-variables.scss'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import { Auth0 } from './auth'

async function init () {
  const AuthPlugin = await Auth0.init({
    onRedirectCallback: (appState) => {
      router.push(
        appState && appState.targetUrl
          ? appState.targetUrl
          : window.location.pathname,
      )
    },
    clientId: process.env.VUE_APP_AUTH0_CLIENT_ID!,
    domain: process.env.VUE_APP_AUTH0_DOMAIN!,
    redirectUri: 'https://meatij.github.io/aimeat-app-beta/',
  })

  createApp(App)
    .provide('BackendUrl', process.env.VUE_APP_BACKEND_URL!)
    .use(store)
    .use(router)
    .use(AuthPlugin)
    .use(ElButton)
    .use(ElCol)
    .use(ElCarousel)
    .use(ElCarouselItem)
    .use(ElDivider)
    .use(ElForm)
    .use(ElFormItem)
    .use(ElIcon)
    .use(ElImage)
    .use(ElInput)
    .use(ElLoading)
    .use(ElRow)
    .use(ElTable)
    .use(ElTableColumn)
    .use(ElUpload)
    .mount('#app')
}

init()
