
import axios from 'axios'
import { defineComponent, inject, reactive, ref } from 'vue'
import { AuthPlugin } from '@/auth'
import EchoImageUpload from '@/components/EchoImageUpload.vue'

interface Example {
  id?: number,
  image?: string,
  url?: string,
  bms?: number,
  trapeziusThickness?: number,
  trapeziusSubcutaneousFatThickness?: number,
  subcutaneousFatThickness?: number,
  latissimusThickness?: number,
}

export default defineComponent({
  name: 'Prediction',
  components: { EchoImageUpload },
  setup () {
    const l2gh = ref<typeof EchoImageUpload>()
    const l2gl = ref<typeof EchoImageUpload>()
    const l3gh = ref<typeof EchoImageUpload>()
    const l3gl = ref<typeof EchoImageUpload>()
    const isRunning = ref(false)
    const predictedBms = ref()
    const predictionUri = ref()
    const searchBms = ref()
    const searchTrapeziusThickness = ref()
    const searchTrapeziusSubcutaneousFatThickness = ref()
    const searchSubcutaneousFatThickness = ref()
    const searchLatissimusThickness = ref()
    const similarExamples = reactive<Example[]>([{}])
    const carouselReloadKey = ref(0)

    const backendUrl: string | undefined = inject('BackendUrl')
    if (!backendUrl) {
      throw new Error('BackendUrl is not provided')
    }

    const auth: AuthPlugin | undefined = inject('Auth')
    if (!auth) {
      throw new Error('Auth plugin is not provided')
    }

    const predict = async () => {
      try {
        console.log('Start prediction.')
        isRunning.value = true

        const data = new FormData()
        l2gh.value?.uploadFile && data.append(l2gh.value.name, l2gh.value.uploadFile)
        l2gl.value?.uploadFile && data.append(l2gl.value.name, l2gl.value.uploadFile)
        l3gh.value?.uploadFile && data.append(l3gh.value.name, l3gh.value.uploadFile)
        l3gl.value?.uploadFile && data.append(l3gl.value.name, l3gl.value.uploadFile)

        const idToken = (await auth.getIdTokenClaims({})).__raw

        const response = await axios.post(`${backendUrl}/v1/prediction`, data, {
          headers: {
            'content-type': 'multipart/form-data',
            Authorization: `Bearer ${idToken}`,
          },
        })
        console.log(response)
        predictedBms.value = response.data.BMS
        searchBms.value = predictedBms.value
        predictionUri.value = response.headers['x-aimeat-predictionuri']
      } finally {
        isRunning.value = false
      }
    }

    const search = async () => {
      console.log('Start similar examples search.')

      if (
        !searchBms.value ||
        !searchTrapeziusThickness.value ||
        !searchTrapeziusSubcutaneousFatThickness.value ||
        !searchSubcutaneousFatThickness.value ||
        !searchLatissimusThickness.value
      ) {
        return
      }

      const idToken = (await auth.getIdTokenClaims({})).__raw
      const bms = parseFloat(searchBms.value)
      const tt = parseFloat(searchTrapeziusThickness.value)
      const tsft = parseFloat(searchTrapeziusSubcutaneousFatThickness.value)
      const sft = parseFloat(searchSubcutaneousFatThickness.value)
      const lt = parseFloat(searchLatissimusThickness.value)

      const response = await axios.get(`${backendUrl}/v1/example`, {
        headers: {
          Authorization: `Bearer ${idToken}`,
          'X-AIMEAT-PredictionUri': predictionUri.value,
        },
        params: { bms, tt, tsft, sft, lt },
      })
      console.log(response)
      similarExamples.splice(0, similarExamples.length, ...response.data)

      // Force re-render carousel
      carouselReloadKey.value++
    }

    const exampleToTableData = (ex: Example) => {
      return [
        { name: 'BMS', value: ex.bms || '-' },
        { name: '僧帽筋厚さ', value: ex.trapeziusThickness || '-' },
        { name: '僧帽筋上皮皮下脂肪厚さ', value: ex.trapeziusSubcutaneousFatThickness || '-' },
        { name: '皮下脂肪厚さ', value: ex.subcutaneousFatThickness || '-' },
        { name: '広背筋厚さ', value: ex.latissimusThickness || '-' },
      ]
    }

    return {
      l2gh,
      l2gl,
      l3gh,
      l3gl,
      isRunning,
      predictedBms,
      searchBms,
      searchTrapeziusThickness,
      searchTrapeziusSubcutaneousFatThickness,
      searchSubcutaneousFatThickness,
      searchLatissimusThickness,
      similarExamples,
      carouselReloadKey,
      predict,
      search,
      exampleToTableData,
    }
  },
})
