<template>
  <div class="prediction">
    <!-- TODO: Separate bms prediction & similar examples search to other components -->
    <h1>BMS推定・類似画像検索</h1>
    <el-row>
      <el-col>
        <el-form>
          <el-row type="flex" justify="center" :gutter="10">
            <el-col :xl="3" :lg="4" :md="6" :sm="8" :xs="10">
              <el-form-item>
                <echo-image-upload ref="l2gh" name="l2gh">L2 高ゲイン</echo-image-upload>
              </el-form-item>
            </el-col>
            <el-col :xl="3" :lg="4" :md="6" :sm="8" :xs="10">
              <el-form-item>
                <echo-image-upload ref="l2gl" name="l2gl">L2 低ゲイン</echo-image-upload>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row type="flex" justify="center" :gutter="10">
            <el-col :xl="3" :lg="4" :md="6" :sm="8" :xs="10">
              <el-form-item>
                <echo-image-upload ref="l3gh" name="l3gh">L3 高ゲイン</echo-image-upload>
              </el-form-item>
            </el-col>
            <el-col :xl="3" :lg="4" :md="6" :sm="8" :xs="10">
              <el-form-item>
                <echo-image-upload ref="l3gl" name="l3gl">L3 低ゲイン</echo-image-upload>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row type="flex" justify="center">
            <el-col>
              <el-form-item>
                <el-button type="primary" @click="predict" :disabled="isRunning">
                  BMS推定
                </el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <div class="predicted-bms">
          <span class="predicted-bms__label">BMS =</span>
          <div v-loading="isRunning" style="display: inline-block">
            <span class="predicted-bms__value">{{ predictedBms ? predictedBms : '?' }}</span>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-row>
      <el-col>
        <el-form label-width="200px" @keyup.enter="search">
          <el-row type="flex" justify="center">
            <el-col :xl="6" :lg="8" :md="10" :sm="12" :xs="14">
              <el-form-item label="BMS">
                <el-input v-model="searchBms" type="number" min="1" max="12"></el-input>
              </el-form-item>
              <el-form-item label="僧帽筋厚さ">
                <el-input v-model="searchTrapeziusThickness" type="number" min="0">
                  <template #append>cm</template>
                </el-input>
              </el-form-item>
              <el-form-item label="僧帽筋上皮下脂肪厚さ">
                <el-input v-model="searchTrapeziusSubcutaneousFatThickness" type="number" min="0">
                  <template #append>cm</template>
                </el-input>
              </el-form-item>
              <el-form-item label="皮下脂肪厚さ">
                <el-input v-model="searchSubcutaneousFatThickness" type="number" min="0">
                  <template #append>cm</template>
                </el-input>
              </el-form-item>
              <el-form-item label="広背筋厚さ">
                <el-input v-model="searchLatissimusThickness" type="number" min="0">
                  <template #append>cm</template>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row type="flex" justify="center">
            <el-col>
              <el-button type="primary" @click="search">類似画像検索</el-button>
            </el-col>
          </el-row>
        </el-form>
      </el-col>
    </el-row>
    <el-row style="margin-top: 20px" type="flex" justify="center">
      <el-col :xl="18" :lg="20" :md="22" :sm="24" :xs="24">
        <el-carousel
          height="400px"
          indicator-position="outside"
          arrow="always"
          :autoplay="false"
          :key="carouselReloadKey"
        >
          <el-carousel-item v-for="example in similarExamples" :key="example">
            <el-row>
              <el-col :span="16">
                <el-image
                  :src="example.image"
                  :preview-src-list="[example.image]"
                  append-to-body
                  hide-on-click-modal
                  fit="scale-down"
                  style="width: 600px; height: 400px"
                >
                  <template #error>
                    <div class="error-image-slot">
                      <i class="el-icon-picture-outline"></i>
                    </div>
                  </template>
                </el-image>
              </el-col>
              <el-col :span="8">
                <el-table
                  height="400px"
                  :data="exampleToTableData(example)"
                  :show-header="false"
                  style="width: 100%"
                >
                  <el-table-column prop="name"></el-table-column>
                  <el-table-column prop="value"></el-table-column>
                </el-table>
              </el-col>
            </el-row>
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import axios from 'axios'
import { defineComponent, inject, reactive, ref } from 'vue'
import { AuthPlugin } from '@/auth'
import EchoImageUpload from '@/components/EchoImageUpload.vue'

interface Example {
  id?: number,
  image?: string,
  url?: string,
  bms?: number,
  trapeziusThickness?: number,
  trapeziusSubcutaneousFatThickness?: number,
  subcutaneousFatThickness?: number,
  latissimusThickness?: number,
}

export default defineComponent({
  name: 'Prediction',
  components: { EchoImageUpload },
  setup () {
    const l2gh = ref<typeof EchoImageUpload>()
    const l2gl = ref<typeof EchoImageUpload>()
    const l3gh = ref<typeof EchoImageUpload>()
    const l3gl = ref<typeof EchoImageUpload>()
    const isRunning = ref(false)
    const predictedBms = ref()
    const predictionUri = ref()
    const searchBms = ref()
    const searchTrapeziusThickness = ref()
    const searchTrapeziusSubcutaneousFatThickness = ref()
    const searchSubcutaneousFatThickness = ref()
    const searchLatissimusThickness = ref()
    const similarExamples = reactive<Example[]>([{}])
    const carouselReloadKey = ref(0)

    const backendUrl: string | undefined = inject('BackendUrl')
    if (!backendUrl) {
      throw new Error('BackendUrl is not provided')
    }

    const auth: AuthPlugin | undefined = inject('Auth')
    if (!auth) {
      throw new Error('Auth plugin is not provided')
    }

    const predict = async () => {
      try {
        console.log('Start prediction.')
        isRunning.value = true

        const data = new FormData()
        l2gh.value?.uploadFile && data.append(l2gh.value.name, l2gh.value.uploadFile)
        l2gl.value?.uploadFile && data.append(l2gl.value.name, l2gl.value.uploadFile)
        l3gh.value?.uploadFile && data.append(l3gh.value.name, l3gh.value.uploadFile)
        l3gl.value?.uploadFile && data.append(l3gl.value.name, l3gl.value.uploadFile)

        const idToken = (await auth.getIdTokenClaims({})).__raw

        const response = await axios.post(`${backendUrl}/v1/prediction`, data, {
          headers: {
            'content-type': 'multipart/form-data',
            Authorization: `Bearer ${idToken}`,
          },
        })
        console.log(response)
        predictedBms.value = response.data.BMS
        searchBms.value = predictedBms.value
        predictionUri.value = response.headers['x-aimeat-predictionuri']
      } finally {
        isRunning.value = false
      }
    }

    const search = async () => {
      console.log('Start similar examples search.')

      if (
        !searchBms.value ||
        !searchTrapeziusThickness.value ||
        !searchTrapeziusSubcutaneousFatThickness.value ||
        !searchSubcutaneousFatThickness.value ||
        !searchLatissimusThickness.value
      ) {
        return
      }

      const idToken = (await auth.getIdTokenClaims({})).__raw
      const bms = parseFloat(searchBms.value)
      const tt = parseFloat(searchTrapeziusThickness.value)
      const tsft = parseFloat(searchTrapeziusSubcutaneousFatThickness.value)
      const sft = parseFloat(searchSubcutaneousFatThickness.value)
      const lt = parseFloat(searchLatissimusThickness.value)

      const response = await axios.get(`${backendUrl}/v1/example`, {
        headers: {
          Authorization: `Bearer ${idToken}`,
          'X-AIMEAT-PredictionUri': predictionUri.value,
        },
        params: { bms, tt, tsft, sft, lt },
      })
      console.log(response)
      similarExamples.splice(0, similarExamples.length, ...response.data)

      // Force re-render carousel
      carouselReloadKey.value++
    }

    const exampleToTableData = (ex: Example) => {
      return [
        { name: 'BMS', value: ex.bms || '-' },
        { name: '僧帽筋厚さ', value: ex.trapeziusThickness || '-' },
        { name: '僧帽筋上皮皮下脂肪厚さ', value: ex.trapeziusSubcutaneousFatThickness || '-' },
        { name: '皮下脂肪厚さ', value: ex.subcutaneousFatThickness || '-' },
        { name: '広背筋厚さ', value: ex.latissimusThickness || '-' },
      ]
    }

    return {
      l2gh,
      l2gl,
      l3gh,
      l3gl,
      isRunning,
      predictedBms,
      searchBms,
      searchTrapeziusThickness,
      searchTrapeziusSubcutaneousFatThickness,
      searchSubcutaneousFatThickness,
      searchLatissimusThickness,
      similarExamples,
      carouselReloadKey,
      predict,
      search,
      exampleToTableData,
    }
  },
})
</script>

